}

.info-link{
  height:50px !important;
  left:55vw;
}

.page-number{
  height:50px !important;
  left:45vw;
}

@media (max-width: 700px) {
  .info-link {
    height: 50px !important;
    left: 60vw;
    font-size:1em;
  }

  .page-number {
    height: 50px !important;
    left: 40vw;
    font-size:1em;
  }
}

  .grid-viewer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(75px, 1fr));
    gap: 40px;
    justify-items: space-between;
    padding: 10px;
    align-items: start;
    padding: 10% 10%;
  }

  @media (max-width: 700px) {
    .grid-viewer {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
      gap: 25px;
      justify-items: space-between;
      padding: 10px;
      align-items: start;
      padding: 10% 10%;
    }
  }

  .grid-viewer img {
    width: auto;
    height: auto;
    max-width: 100%; /* Ensures image does not overflow its grid cell */
    max-height: 100vh;
    opacity: 0;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;
  }

    .grid-viewer img:hover {
      opacity: 80%; /* scale up image to 110% of its original size on hover */
    }

  .grid-cell {
    display: flex;
    align-items: center; /* Centers content vertically */
    justify-content: center; /* Centers content horizontally */
    height: 100%; /* Optional: Specify a fixed height if needed */
  }

  .info-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
    background: #fff; /* Light grey background */
    color: #333; /* Dark grey text */
    cursor: pointer;
  }

    .info-page h1 {
      margin: 0.5em 0;
      font-size: 1.2em; /* 32px if the user's default font-size is 16px */
      color: #000;
      font-family: 'Cormorant Garamond', sans-serif;
      font-weight: 700;
      opacity: 0;
      animation: fadeIn 1s ease-in-out forwards;
    }


    .info-page p {
      font-size: 1em;
      color: #666; /* Lighter grey text */
      margin: 0px;
      font-family: 'Cormorant Garamond', sans-serif;
      font-weight: 500;
      opacity: 0;
      animation: fadeIn 1s ease-in-out forwards;
    }
